<template>
  <header class="hdr">
    <div class="container">
      <div class="body">
        <button class="brg" @click="toggleSideBar">
          <span class="material-symbols-sharp">menu</span>
        </button>
        <nav class="nav">
          <router-link
            v-for="link in links"
            :key="link.title"
            :to="link.path"
            class="link"
            active-class="router-active"
          >
            <span class="material-symbols-outlined icn">{{ link.symbol }}</span>
            <p>{{ link.title }}</p>
          </router-link>
        </nav>
        <button class="btn" @click="$router.push('/account')">
          <span class="material-symbols-rounded">account_circle</span>
        </button>
        <v-sidebar :state="panelState"></v-sidebar>
      </div>
    </div>
  </header>
</template>

<script>
import vSidebar from "./v-sidebar.vue"
import links from "@/components/static/header-static-links.js"
export default {
  name: "HeaderPage",
  components: { vSidebar },
  data() {
    return {
      links,
      panelState: "hidden",
    }
  },
  methods: {
    toggleSideBar() {
      this.panelState == "hidden"
        ? (this.panelState = "active")
        : (this.panelState = "hidden")
    },
  },
}
</script>

<style lang="scss" scoped>
.hdr {
  width: 100%;
  height: auto;
  padding: 30px 0 30px;
}
.body {
  position: relative;
  width: 100%;
  padding: 5px 5px 5px 25px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  background: $topdowncolors;
  border-radius: 15px;
  box-shadow: 0 0 60px $blocksshadows;
}
.nav {
  display: flex;
  align-items: center;
}
.link {
  display: flex;
  align-items: center;
  text-decoration: none;
  margin: 0 20px;
  font-size: 16px;
  font-family: "Inter", sans-serif;
  color: #fff;
  font-weight: 400;
  img {
    margin-right: 10px;
  }
  &:hover {
    filter: drop-shadow(0 0 2px #eece25);
  }
}
.btn {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 60px;
  height: 50px;
  background: $btncolor;
  border-radius: 15px;
  border: 1px solid $topdowncolors;
  box-shadow: 0 0 10px rgb(0, 0, 0, 0.25);
  transition: 0.3s ease 0s;
  cursor: pointer;
  & > span {
    font-size: 35px;
    color: #fff;
  }
  &:hover {
    transform: scale(0.9);
  }
}
.brg {
  display: none;
  display: flex;
  justify-content: center;
  align-items: center;
  border: none;
  background: transparent;
  & > span {
    font-size: 30px;
    color: #fff;
  }
}
.icn {
  margin-right: 10px;
}

@media (max-width: 885px) {
  .brg {
    display: inline-flex;
  }
  .hdr {
    position: fixed;
    bottom: 0px;
    padding: 20px 0 20px;
  }
  .body {
    position: static;
    width: unset;
    width: auto;
    justify-content: flex-start;
  }

  .brg {
    margin-right: 20px;
  }
  .nav {
    display: none;
    position: absolute;
    z-index: 110000;
    align-items: center;
    justify-content: center;
    width: 100vw;
    height: 100vh;
    background: $topdowncolors;
    position: fixed;
    top: 0;
    left: 0;
    flex-direction: column;
    & > a {
      margin-bottom: 20px;
      font-size: 20px;
      & > span {
        font-size: 30px;
      }
    }
  }
}
</style>
