<template>
  <main class="blog">
    <div class="container">
      <h1 class="title bt1" data-shadow="Блог">Блог</h1>
      <h1 class="bt title" style="text-align: center">
        Ниже вы можете увидеть мои посты
      </h1>
      <div class="wrp">
        <div class="w" v-for="item in posts" :key="item.id">
          <v-post
            :key="item.id"
            :id="item.id"
            :img="item.img"
            :text="item.text"
            :date="item.date"
            :rating="item.rating"
            @click="pushToPost(item.id)"
          />
        </div>
      </div>
    </div>
  </main>
</template>

<script>
import vPost from "@/components/labels/v-post.vue"
export default {
  name: "BlogPage",
  components: {
    vPost,
  },
  methods: {
    pushToPost(id) {
      this.$router.push({ name: "post", params: { id } })
    },
  },
  computed: {
    posts() {
      return this.$store.getters.getPosts
    },
  },
  created() {
    if (this.$store.state.posts.length == 0) {
      this.$store.dispatch("initApp")
    }
  },
}
</script>

<style scoped lang="scss" src="./blg.scss"></style>
