<template>
  <div class="loading" v-show="!isLoaded"></div>
  <div class="circle-loading" v-show="!isLoaded">
    <span class="material-symbols-outlined"> progress_activity </span>
  </div>
  <div class="wrapper">
    <v-header></v-header>
    <router-view v-slot="{ Component }">
      <transition name="fade">
        <component :is="Component" />
      </transition>
    </router-view>
    <v-footer></v-footer>
  </div>
</template>

<script>
import vHeader from "@/components/labels/v-header.vue"
import vFooter from "@/components/labels/v-footer.vue"
export default {
  name: "App",
  components: {
    vHeader,
    vFooter,
  },
  data() {
    return {
      isLoaded: false,
    }
  },
  watch: {
    $route(to, from) {
      if (to.name == "post" && from.name == "post") {
        this.isLoaded = true
      } else {
        this.isLoaded = false
        setTimeout(() => (this.isLoaded = true), 1000)
      }
    },
  },
  created() {
    setTimeout(() => {
      this.isLoaded = true
    }, 1000)
  },
}
</script>

<style lang="scss" scoped>
.loading {
  position: absolute;
  left: 0;
  top: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 4px;
  background-image: linear-gradient(90deg, $blocksshadows, $topdowncolors);
  animation: loading 1s ease-in infinite;
}
@keyframes loading {
  0% {
    transform: translate(-100%);
  }
  100% {
    transform: translate(0);
  }
}

.circle-loading {
  display: flex;
  align-items: center;
  justify-content: center;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  width: 100px;
  height: 100px;
}

.material-symbols-outlined {
  animation: circleLoading 1s linear infinite;
  font-size: 45px;
  color: $topdowncolors;
}

@keyframes circleLoading {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
.wrapper {
  display: flex;
  flex-direction: column;
  flex: 1 1 auto;
  overflow-x: hidden;
  overflow-y: auto;
  width: 100%;
  min-height: 100vh;
}

.fade-enter-from,
.fade-leave-to {
  opacity: 0;
}
.fade-enter-to,
.fade-leave-from {
  opacity: 1;
}

.fade-enter-active,
.fade-enter-active {
  transition: all 0.5s ease-in-out 1s;
}
</style>
