<template>
  <section class="tools">
    <div class="container">
      <div class="tools__body">
        <h1 class="title tools__title" data-shadow="Инструменты">
          Инструменты
        </h1>
        <h2 class="tools__text">Мои инструменты в разработке</h2>
      </div>
    </div>
    <div class="tools__content">
      <div class="container">
        <div class="tools__content-body">
          <div class="tools__content-cart" v-for="itm in stack" :key="itm">
            <div class="tools__content-img">
              <img :src="itm.picture" alt="" />
            </div>
            <p class="tools__content-text">{{ itm.name }}</p>
            <p class="tools__content-raiting">
              Используемость:
              <span
                class="material-symbols-outlined"
                v-for="k in 5"
                :key="k"
                :class="{ active: isRated(itm.using, k) }"
              >
                star_rate
              </span>
            </p>
          </div>
        </div>
      </div>
    </div>
  </section>
</template>

<script>
import { stack } from "@/components/static/tools-static.js"
export default {
  name: "tools-section",
  data() {
    return {
      stack,
    }
  },
  methods: {
    isRated(rate, id) {
      return rate - id > 0
    },
  },
}
</script>

<style lang="scss" scoped src="./scss/tools.scss"></style>
