<template>
  <main>
    <div class="container">
      <section class="post">
        <div class="content">
          <h1 class="title pt" data-shadow="Блог">Блог</h1>
          <h2 class="title t">{{ getComputedPost.title }}</h2>
          <time :datetime="getComputedPost.date"
            >Дата публикации: {{ getComputedPost.date }}</time
          >
          <div class="public">
            <p class="post-t">{{ getComputedPost.text }}</p>
            <div class="usefullness">
              <span class="text">Оцените полезность блога:</span>
              <span
                class="material-symbols-outlined star"
                v-for="i in 5"
                :key="i"
                :class="{ active: checkRating(i) }"
                @click="clicked(i)"
              >
                star_rate
              </span>
            </div>
          </div>
        </div>
        <aside class="sider">
          <div
            class="sider-t"
            v-for="post in getPosts"
            :key="post.id"
            :class="{ active: $route.params.id == post.id }"
            @click="toPostId(post.id)"
          >
            <img :src="post.img" alt="" class="img" />
            <span class="sider-text">{{ post.title }}</span>
            <time :datetime="post.date" class="sider-time">{{
              post.date
            }}</time>
          </div>
        </aside>
      </section>
    </div>
  </main>
</template>
<script>
export default {
  name: "PostView",
  data() {
    return {
      rating: 0,
    }
  },
  created() {
    let getLS = localStorage.getItem(`rating-${this.$route.params.id}`)
    this.$store.dispatch("initApp")
    this.rating = getLS ? getLS : 0
  },
  methods: {
    clicked(rate) {
      this.rating = rate
      localStorage.setItem(`rating-${this.$route.params.id}`, this.rating)
    },
    checkRating(index) {
      return this.rating - index >= 0
    },
    toPostId(id) {
      this.$router.push(`/post_${id}`)
    },
  },
  watch: {
    $route() {
      this.rating = localStorage.getItem(`rating-${this.$route.params.id}`)
    },
  },
  computed: {
    getComputedPost() {
      let res = this.$store.getters.getPosts.filter((post) => {
        if (this.$route.params.id == post.id) {
          return post
        }
      })[0]
      return res
    },
    getPosts() {
      return this.$store.getters.getPosts
    },
  },
}
</script>

<style lang="scss" scoped src="./post.scss" />
