import { createRouter, createWebHistory } from "vue-router"
import HomePage from "@/components/screens/home-page/HomeView.vue"
import AboutMe from "@/components/screens/AboutView.vue"
import PortfolioPage from "@/components/screens/PortfolioView.vue"
import BlogPage from "@/components/screens/blog-page/BlogView.vue"
import AccountPage from "@/components/screens/account-page/AccountView.vue"
import ContactsPage from "@/components/screens/ContactsView.vue"
import PostView from "@/components/screens/post-page/PostView.vue"
import ErrorView from "@/components/screens/ErrorView.vue"
const routes = [
  {
    path: "/",
    name: "home",
    alias: "/home",
    component: HomePage,
  },
  {
    path: "/about",
    name: "about",
    component: AboutMe,
  },
  {
    path: "/blog",
    name: "blog",
    alias: ["/blg", "/blo", "/bl", "/bgol", "/bolg", "/bol"],
    component: BlogPage,
  },
  {
    path: "/post_:id",
    name: "post",
    component: PostView,
  },
  {
    path: "/portfolio",
    name: "portfolio",
    component: PortfolioPage,
  },
  {
    path: "/contacts",
    name: "contacts",
    component: ContactsPage,
  },
  {
    path: "/account",
    name: "account",
    component: AccountPage,
  },
  {
    path: "/error",
    name: "error",
    component: ErrorView,
  },
  {
    path: "/:pathMatch(.*)*",
    redirect: "/error",
  },
]

const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes,
})

export default router
