<template>
  <section class="main">
    <div class="container">
      <div class="body">
        <h1 class="animate__fadeIn title mt" data-shadow="Главная">Главная</h1>
        <div class="content">
          <div class="blog">
            <h2 class="bt animate__fadeIn">последние ПОСТЫ</h2>
            <div class="bcont">
              <v-post
                v-for="item in posts.slice(0, 3)"
                :key="item.id"
                :id="item.id"
                :img="item.img"
                :text="item.text"
                :date="item.date"
                :rating="item.rating"
                @click="routeToPost(item.id)"
              />
            </div>
          </div>
          <div class="motivation">
            <h1 class="mott">
              Вы на моём блоге-портфолио. Читайте и наслаждайтесь!
            </h1>
            <router-link to="/portfolio" class="btn">К портфолио</router-link>
          </div>
        </div>
      </div>
    </div>
  </section>
</template>

<script>
import vPost from "@/components/labels/v-post.vue"
export default {
  name: "m-sec",
  components: {
    vPost,
  },
  computed: {
    posts() {
      return this.$store.getters.getPosts
    },
  },
  methods: {
    routeToPost(id) {
      this.$router.push({ name: "post", params: { id } })
    },
  },
  created() {
    if (this.$store.state.posts.length == 0) {
      this.$store.dispatch("initApp")
    }
  },
}
</script>

<style lang="scss" scoped src="./scss/ms.scss"></style>
