import { createStore } from "vuex"
import axios from "axios"

let store = createStore({
  state() {
    return {
      posts: [],
      portfolio: [],
      "about-me": {},
    }
  },
  mutations: {
    setPosts(state, posts) {
      state.posts = posts
    },
    setPortfolio(state, portfolio) {
      state.portfolio = portfolio
    },
    setAbout(state, about) {
      state["about-me"] = about
    },
  },
  actions: {
    initApp({ commit }) {
      axios
        .get("https://dannygolds.github.io/portfolio_new/static/db.json")
        .then((response) => {
          commit(
            "setPosts",
            response.data.posts.sort((firstDate, secondDate) => {
              if (firstDate > secondDate) {
                return 1
              }
              return -1
            })
          )
          commit("setPortfolio", response.data.portfolio)
          commit("setAbout", response.data["about-me"])
        })
    },
  },
  getters: {
    getPosts(state) {
      return state.posts
    },
    getPortfolio(state) {
      return state.portfolio
    },
    getAbout(state) {
      return state["about-me"]
    },
  },
})

export default store
