<template>
  <div class="bp">
    <img :src="img" alt="IMG" class="img" />
    <time class="text" :datetime="date">Дата создания: {{ date }}</time>
    <p class="text">{{ text }}</p>
    <div class="rating">
      <p
        v-for="k in 5"
        :key="k"
        :class="{ 'active-rtg': checkRating(k) }"
        class="star"
      >
        <span class="material-symbols-outlined">star_rate</span>
      </p>
    </div>
  </div>
</template>
<script>
export default {
  name: "vPost",
  methods: {
    checkRating(k) {
      return this.rating - k >= 0
    },
  },
  props: {
    id: {
      type: Number,
      required: true,
    },
    img: {
      type: String,
      default: "@/assets/images/posts.svg",
    },
    text: {
      type: String,
      default: "This is the text of the post",
    },
    rating: {
      type: Number,
      default: 1,
    },
    date: {
      type: String,
      required: true,
    },
  },
}
</script>

<style scoped lang="scss">
.bp {
  max-width: 215px !important;
  width: 100%;
  border-radius: 10px;
  border: 1px solid #fff;
  background: $titleseccolor;
  box-shadow: 0 0 25px rgba(0, 0, 0, 0.15);
  padding: 0 0 25px;
  transition: 0.3s ease 0s;
  max-width: 270px;
  overflow-x: hidden;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  &:last-child {
    margin-right: 0 !important;
  }
  &:hover {
    cursor: pointer;
    transform: scale(1.1);
  }
}
.img {
  width: 100%;
  height: 145px;
  margin-bottom: 30px;
}
.text {
  width: 90%;
  font-family: "Inter", sans-serif;
  font-size: 14px;
  font-weight: 300;
  text-align: justify;
  color: #fff;
  margin-bottom: 15px;
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 2; /* limit to 2 lines */
}
.rating {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 8px 18px;
  border-radius: 5px;
  box-shadow: 0 0 4px rgb(0, 0, 0, 0.25);
  background: rgb(255, 255, 255, 0.6);
}
.star {
  color: #30303050;
  filter: drop-shadow(0 0 3px rgba(0, 0, 0, 0.25));
  font-size: 20px;
  .active-rtg {
    color: #f1ef44;
    filter: drop-shadow(0 0 3px rgba(0, 0, 0, 0.25));
  }
}
</style>
